body {
  font-family: "Sora";
}
.MuiButton-root {
  text-transform: none !important;
}

.backimg {
  margin: -32px 0px 0px -32px;
  width: 100%;
}
.headline {
  position: absolute;
  top: 8px;
  left: 16px;
  font-family: "IBM Plex Sans";
  color: white;
  font-weight: bold;
  font-size: 55px;
}
.headline1 {
  font-family: "IBM Plex Sans";
  color: black;
  font-weight: bold;
  font-size: 55px;
  padding-bottom: 5%;
}
.centered {
  position: absolute;
  top: 50%;
  left: 20%;
  transform: translate(-50%, -50%);
}
.top-right {
  position: absolute;
  top: -19%;
  right: 40%;
}
.bottom-left-div1 {
  position: absolute;
  bottom: 9%;
  left: 16px;
  color: white;
  font-size: small;
}
.bottom-left-div2 {
  position: absolute;
  bottom: 4%;
  left: 16px;
  color: white;
  font-size: medium;
  font-weight: bold;
}
.customBtn {
  background: "linear-gradient(45deg, #4734E1 30%, #8E27EA 90%)";
  border-radius: "100px";
  border: 0;
  color: "white";
  height: 48;
  padding: "0 30px";
  box-shadow: "0 3px 5px 2px rgba(255, 105, 135, .3)";
}
.iconcss {
  position: relative;
  padding-right: 8px;
}
.selected {
  background-color: #a2a2a2 !important;
}
.btn {
  cursor: pointer;
  display: inline-flex;
  user-select: none;
  padding: 11px 37px;
  font-size: 0.875rem;
  min-width: 64px;
  box-sizing: border-box;
  line-height: 1.75;
  letter-spacing: 0.02857em;
  background-color: #e0e0e0;
  margin-right: 32px;
  margin-bottom: 32px;
}

.btn:hover {
  background-color: #d5d5d5;
  text-decoration: none;
}
